<template>
    <div class="board order_frame">
        <h2 class="board cart_head"></h2>
        <div class="board white order_board">
            <div class="user_acc">
                <span class="bold"></span>
                <span class="c999">{{addressInfo.name}}</span>
            </div>
            <div class="user_acc">
                <span class="bold"></span>
                <span class="c999">{{addressInfo.phone}}</span>
            </div>
            <div class="user_acc">
                <span class="bold"></span>
                <span class="c999">{{addressInfo.adr}}</span>
            </div>
        </div>
        <button class="board save_address" @click="setThisAddress(true)" v-show="addressInfo.show == false"></button>
        <div class="board white" v-show="addressInfo.show">
            <div class="server_list"></div>
            <input type="text" class="set_address" v-model="addressInfo.name" placeholder="" />
            <input type="text" class="set_address" v-model="addressInfo.phone" placeholder="" />
            <input type="text" class="set_address" v-model="addressInfo.adr" placeholder="" />
            <button class="board save_address" @click="setThisAddress(false)"></button>
        </div>
        <ul class="board white list_pay">
            <div class="server_list"></div>
            <li class="board" v-for="(item, index) in listToPay" :key="index">
                <div class="board pay_img">
                    <img :src="listToPay[index].img" width="100%" height="auto" />
                </div>
                <div class="board pay_item_info">
                    <h3 class="board">{{listToPay[index].name}}</h3>
                    <h4 class="board">{{listToPay[index].price}}</h4>
                    <p>{{listToPay[index].num}}</p>
                </div>
            </li>
        </ul>
        <div class="order_total_pay">
            <p>
                <span></span>
                <span class="red">{{totalPay}}</span>
            </p>
            <button @click="submitOrder()"></button>
        </div>
    </div>
</template>

<style scoped>
    @import "../css/Order.css";
</style>

<script>
    export default {
        name: "Order",
        data() {
            return {
                addressInfo: {
                    show: false,
                    name: "",
                    phone: "",
                    adr: ""
                },
                listToPay: this.$root.$children[0].orderList,
                totalPay: 0.00
            }
        },
        mounted() {
            let m = 0;
            for (let i = 0; i < this.listToPay.length; i++) {
                m = m + Number(this.listToPay[i].price) * Number(this.listToPay[i].num)
            };
            this.totalPay = m.toFixed(2)
        },
        methods: {
            setThisAddress(n) {
                if (n == false) {
                    if (this.addressInfo.name.length < 1) {
                        this.$root.$children[0].showToast("");
                        return
                    };
                    if (this.addressInfo.phone.length < 1) {
                        this.$root.$children[0].showToast("");
                        return
                    };
                    if (this.addressInfo.adr.length < 1) {
                        this.$root.$children[0].showToast("");
                        return
                    }
                };
                this.addressInfo.show = n
            },  //

            submitOrder() {
                if (this.addressInfo.name.length < 1) {
                    this.$root.$children[0].showToast("");
                    return
                };
                if (this.addressInfo.phone.length < 1) {
                    this.$root.$children[0].showToast("");
                    return
                };
                if (this.addressInfo.adr.length < 1) {
                    this.$root.$children[0].showToast("");
                    return
                };
                if (Number(localStorage.getItem("money" + this.$root.$children[0].nowAccount.num)) < Number(this.totalPay)) {
                    this.$root.$children[0].showToast("");
                    return
                };
                let b = Number(localStorage.getItem("money" + this.$root.$children[0].nowAccount.num)) - this.totalPay;
                localStorage.setItem("money" + this.$root.$children[0].nowAccount.num, b);
                let obj = {type: "", money: this.totalPay, time: this.$root.$children[0].rgtTime()};
                this.$root.$children[0].bill.unshift(obj);
                this.$root.$children[0].showToast("");
                this.$root.$children[0].listFromCart = [];
                this.$root.$children[0].orderList = [];
                this.$router.push("/mine")
            }
        },
        computed: {},
        components: {}
    }
</script>