<template>
    <div class="board page_goods_detail">
        <div class="board goods_imgs" :style="'background-image:url(' + slider.img[slider.index] +')'" @touchstart="m1($event)" @touchend="m2($event)">
            <div class="goods_img_num">
                <span>{{slider.index + 1}}</span>
                <span>/</span>
                <span>{{slider.img.length}}</span>
            </div>
        </div>
        <div class="board goods_bg">
            <div class="goods_price">
                <h3>
                    <span></span>
                    <span>{{storeInfo.goodsPrice}}</span>
                </h3>
                <h4>
                    <span></span>
                    <span>{{storeInfo.vipPrice}}</span>
                    <span class="opacity">_</span>
                    <span class="price_vip">VIP</span>
                </h4>
                <a href="javascript:void(0)" class="goods_share" @click="openImg()"></a>
            </div>
            <h2 class="board goods_title_name">{{storeInfo.goodsName}}</h2>
            <div class="board goods_detail_info">
                <p>{{storeInfo.otPrice}}</p>
                <p>{{storeInfo.stock}}</p>
                <p>{{storeInfo.sales}}</p>
            </div>
        </div>
        <div class="board select_goods_info" v-show="false">
            <p></p>
            <h5>
                <span>1</span>
                <span>2</span>
                <span>3</span>
            </h5>
            <a href="javascript:void(0)">&gt;</a>
        </div>
        <div class="board select_goods_info">
            <h5>
                <span></span>
                <span>0</span>
            </h5>
            <a href="javascript:void(0)">&gt;</a>
        </div>
        <div class="board goods_bg">
            <h3 class="board goods_cm_title"></h3>
            <ul class="board goods_cm_list">
                <li class="board" v-for="(item, index) in goodsCM.list" :key="index" v-show="index < goodsCM.showNum" :value="goodsCM.list[index].id" @click="toDetail($event)">
                    <div class="board goods_cm_img">
                        <img :src="goodsCM.list[index].image" width="100%" height="auto" :value="goodsCM.list[index].id" />
                    </div>
                    <p class="board goods_cm_des" :value="goodsCM.list[index].id">{{goodsCM.list[index].store_name}}</p>
                    <p class="board goods_cm_price" :value="goodsCM.list[index].id">{{goodsCM.list[index].price}}</p>
                </li>
            </ul>
            <button class="board more_cm" @click="loadMore($event)"></button>
        </div>
        <input type="text" value="" id="copy" />
        <div class="board goods_bg t01">
            <h3 class="board goods_des_title"></h3>
            <div class="board" v-html="desImgs"></div>
        </div>
        <div class="foot_fn">
            <a href="javascript:void(0)" @click="pageBack()"><br/></a>
            <a href="javascript:void(0)" @click="copyShare()"><br/></a>
            <a href="javascript:void(0)" @click="toPageCart()"><br/></a>
            <button @click="addToCart()"></button>
        </div>
    </div>
</template>

<style scoped>
    @import "../css/Detail.css";
</style>

<script>
    import axios from "../../axios";  //axios

    export default {
        name: "Detail",
        data() {
            return {
                slider: {
                    img: [],
                    index: 0
                },
                goodsCM: {
                    list: [],
                    showNum: 6
                },
                storeInfo: {
                    goodsName: "",
                    goodsPrice: 0,
                    vipPrice: 0,
                    otPrice: 0,
                    stock: 0,
                    sales: 0
                },
                desImgs: "",
                move1: 0,
                move2: 0,
                goodsID: ""
            }
        },
        mounted() {
            const that = this;
            const appData = that.$root.$children[0];
            document.getElementById("nav").style.display = "none";  //
            that.setAllInfo()
        },
        methods: {
            pageBack() {
                this.$router.push("/")
            },  //

            setAllInfo() {
                const that = this;
                let goodsID = sessionStorage.getItem("goodsID");
                axios.get('http://47.115.51.185/api/product/detail/' + goodsID).then((res) => {
				    console.log("", res);
                    window.scrollTo(0,0);
                    that.slider.img = res.data.data.storeInfo.slider_image;  //
                    that.goodsCM.list = res.data.data.good_list;  //
                    that.storeInfo.goodsName = res.data.data.storeInfo.store_name;  //
                    that.storeInfo.goodsPrice = res.data.data.storeInfo.price;  //
                    that.storeInfo.vipPrice = res.data.data.storeInfo.vip_price; //
                    that.storeInfo.otPrice = res.data.data.storeInfo.ot_price;  //
                    that.storeInfo.stock = res.data.data.storeInfo.stock;  //
                    that.storeInfo.sales = res.data.data.storeInfo.fsales;  //
                    that.desImgs = res.data.data.storeInfo.description;  //
                    that.goodsID = res.data.data.storeInfo.id  //ID
			    }).catch((err) => {
				    console.log(err)
			    })
            },

            m1(e) {
                this.move1 = e.changedTouches[0].pageX
            },
            m2(e) {
                this.move2 = e.changedTouches[0].pageX;
                if (this.move1 < this.move2) {
                    this.goodsImgChange(-1)
                } else if (this.move1 > this.move2) {
                    this.goodsImgChange(1)
                }
            },  //

            goodsImgChange(n) {
                if (this.slider.img.length > 1) {
                    let change = this.slider.index + n;
                    if (change > this.slider.img.length - 1) {
                        this.slider.index = 0
                    } else if (change < 0) {
                        this.slider.index = this.slider.img.length - 1
                    } else {
                        this.slider.index = change
                    }
                }
            },  //

            openImg() {
                window.open(this.slider.img[this.slider.index])
            },  //

            copyShare() {
                if (this.storeInfo.goodsName.length > 0) {
                    let c = this.storeInfo.goodsName;
                    let b = document.getElementById("copy");
                    b.value = c;
                    b.select();
                    document.execCommand("Copy");
                    this.$root.$children[0].showToast("")
                }
            },  //

            loadMore(e) {
                if (this.goodsCM.showNum < this.goodsCM.list.length) {
                    this.goodsCM.showNum = this.goodsCM.showNum + 6
                } else {
                    let q = confirm("");
                    if (q == true) {
                        this.goodsCM.showNum = 6
                    }
                }
            },  //
            
            toDetail(e) {
                sessionStorage.setItem("goodsID", e.path[0].getAttribute("value"));
                this.setAllInfo()
            },  //

            toPageCart() {
                this.$router.push("/cart");
                document.getElementById("nav").style.display = "flex"
            },  //

            addToCart() {
                const that = this;
                if (that.slider.img.length < 1) {
                    return
                };  //
                if (that.storeInfo.goodsName.length < 1) {
                    return
                };  //
                if (that.goodsID == "") {
                    return
                };  //ID

                if (this.$root.$children[0].isLogin == false) {
                    this.$root.$children[0].showToast("");
                    this.$router.push("/login")
                } else {
                    let addIt = () => {
                        let obj = {
                            name: that.storeInfo.goodsName,  //
                            img: that.slider.img[0],  //
                            price: that.storeInfo.goodsPrice,  //
                            id: that.goodsID,  //ID
                            select: false,  //
                            num: 1  //
                        };
                        that.$root.$children[0].listFromCart.push(obj);
                        console.log(that.$root.$children[0].listFromCart);
                        that.$root.$children[0].showToast("")
                    };

                    if (that.$root.$children[0].listFromCart.length < 1) {
                        addIt()
                    } else if (that.$root.$children[0].listFromCart.length >= 1) {
                        for (let i = 0; i < that.$root.$children[0].listFromCart.length; i++) {
                            if (that.goodsID == that.$root.$children[0].listFromCart[i].id) {
                                that.$root.$children[0].showToast("");
                                return;
                                break
                            }  //
                        };
                        addIt()
                    }
                }
            }  //
        },
        computed: {},
        components: {}
    }
</script>